import {
  erc20ABI,
  bondABI,
  transferAddress,
  srsAddress,
  usdtAddress
} from '@/util/casinoContract.js'
import {
  newBlockchainConnection
} from '@/util/getWeb.js'
import { getUserAddress } from '@/util/bridge'
import web3 from 'web3'
import store from "./store";
import * as util from './util/util.js'


let WEB = ''
let address = ''
init()

function init () {
  console.log(1)
  window.onload = async () => {
    console.log(2)
    WEB = newBlockchainConnection(web3.givenProvider || 'https://data-seed-prebsc-1-s1.binance.org:8545')
    console.log('WEB', WEB)
    await store.dispatch('ActionsSetWEB3', WEB)
    content()
  }
}

async function content () {
  console.log('获取地址中...')
  console.log('获取的地址', await WEB.eth.getAccounts((res) => console.log(res)))
  // requestAccounts
  await WEB.eth.getAccounts().then(async (res, err) => {
    console.log('获取的地址', res)
    if (!err) {
      // if (res.length === 0) {
      //   let res = await getUserAddress()
      //   address = res.address
      //   store.dispatch('ActionsGetAddress', address)
      // } else {
      //   address = res[0]
      //   store.dispatch('ActionsGetAddress', address)
      // }
      address = res[0]
      store.dispatch('ActionsGetAddress', address)
      // 实例化srs
      let srscontractInstance = new WEB.eth.Contract(
        erc20ABI,
        srsAddress
      )
      store.dispatch(
        'ActionsSetsrscontractInstance',
        srscontractInstance
      )
      // 实例化USDT合约
      let usdtContractInstance = new WEB.eth.Contract(
        erc20ABI,
        usdtAddress
      )
      store.dispatch(
        'ActionsSetUsdtContractInstance',
        usdtContractInstance
      )
      // 转账合约

      let TrContractInstance = new WEB.eth.Contract(
        bondABI,
        transferAddress
      )
      store.dispatch(
        'ActionsSetTransfer',
        TrContractInstance
      )
      setTimeout(() => {
        console.log('缓存', store.state)
      }, 1000);

      await getBalanceOfSRS(srscontractInstance, address)
      await getBalanceOfUSDT(usdtContractInstance, address)
      status = true
    }
  }).catch((error) => {
    console.error('初始化合约错误', error)
  })
}

// SRS授权余额
async function getBalanceOfSRS (contractInstance, val) {
  let balance = await contractInstance.methods
    .allowance(val, transferAddress)
    .call()
  balance = util.KeepDecimal((balance / Math.pow(10, 18)), 4)
  store.dispatch('ActionsSetSRSbalance', balance)
}
// USDT授权余额
async function getBalanceOfUSDT (contractInstance, val) {
  let balance = await contractInstance.methods
    .allowance(val, transferAddress)
    .call()
  balance = util.KeepDecimal((balance / Math.pow(10, 18)), 4)
  store.dispatch('ActionsSetUSDTbalance', balance)
}